import { CurrencyPipe, DatePipe, PercentPipe } from '@angular/common';
import { AfterViewInit, ChangeDetectorRef, Component, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { PublisherChartDataInterface } from 'src/app/shared/models/publisher.charData.interface';
import { PublisherAPIService } from 'src/app/shared/service/admin-stats/publisher-api.service';
import { ExportDataService } from 'src/app/shared/service/export-data.service';
import { UsersService } from 'src/app/shared/service/users.service';
import { TagManagementService } from '../../tag-management/tag-management.service';

@Component({
  selector: 'app-tag-api',
  templateUrl: './tag-api.component.html',
  styleUrls: ['./tag-api.component.scss']
})
export class TagApiComponent implements AfterViewInit {
  @ViewChild('expandableTable') table: any;
  loadingIndicator = true;
  paramTagId: string;
  paramApiKey: string;
  selectedCompany: any;
  statData: any = [];
  range = {
    startDate: '',
    endDate: '',
  };
  tagRows: any;
  chartData: PublisherChartDataInterface;
  constructor(
    private route: ActivatedRoute,
    private cdr: ChangeDetectorRef,
    private userService: UsersService,
    private publisherApiService: PublisherAPIService,
    private exportDataService: ExportDataService,
    private datePipe: DatePipe,
    private currencyPipe: CurrencyPipe,
    private percentPipe: PercentPipe,
    private tagManagementService: TagManagementService,
  ) { 
    this.selectedCompany = this.getSelectedCompanyFromLocalStorage();
  }

  async ngAfterViewInit() {
    this.route.params.subscribe(async routeParams => {
      this.paramTagId = routeParams.tagId;
      this.paramApiKey = routeParams.apiKey;
      this.tagRows = await this.getTagInformation(routeParams.tagId);
      this.statData = [];
      this.statData = await this.getPublisherAPIData(this.selectedCompany, this.paramApiKey, this.paramTagId, this.range.startDate, this.range.endDate);
      this.chartData = await this.getChartMetrics(this.selectedCompany, this.paramApiKey, this.paramTagId, this.range.startDate, this.range.endDate);
      this.loadingIndicator = false;
      this.cdr.markForCheck();
    });
  }

  getTagInformation(id: any) {
    return this.tagManagementService.getOneTag(id).toPromise().then((response) => {
      return response;
    }).catch((error) => {
      return error;
    })
  }

  async getChartMetrics(company: any, apiKey: any, tagId: any, startDate: any, endDate: any) {
    try {
      const response = await this.publisherApiService.getPublisherAPIStats(company, apiKey, tagId, startDate, endDate).toPromise();

      var helperChart = {};
      var resultChart = response.reduce(function (r, o) {
        var key = o.date;
        if (!helperChart[key]) {
          helperChart[key] = Object.assign({}, o); // create a copy of o
          r.push(helperChart[key]);
        } else {
          helperChart[key].searches += parseInt(o.searches);
          if (o.revenue) {
            helperChart[key].revenue += parseFloat(o.revenue);
          }
        }
        return r;
      }, []);
      
      var revenuePerDayVal = [];
      var publisherRevenuePerDayVal = [];
      var datesOfRevenueVal = [];
      var searchesPerDayVal = [];
      var chartDataValue = {};
      for (var resVal of resultChart) {
        revenuePerDayVal.push(0);
        publisherRevenuePerDayVal.push(resVal.revenue);
        datesOfRevenueVal.push(resVal.date);
        searchesPerDayVal.push(resVal.searches);
      }
      chartDataValue['revenuePerDay'] = revenuePerDayVal;
      chartDataValue['publisherRevenuePerDay'] = publisherRevenuePerDayVal;
      chartDataValue['datesOfRevenue'] = datesOfRevenueVal;
      chartDataValue['searchesPerDay'] = searchesPerDayVal;
      return chartDataValue;
    } catch (error) {
      return error;
    }

  }

  exportToCsv(): void {
    let exportCsv = [];
    for (var statOne of this.statData) {
      let stat = {
        date: this.datePipe.transform(statOne.date, "dd MMM yyyy"),
        subid: statOne.subid,
        country: statOne.country,
        clicks: statOne.clicks,
        searches: statOne.searches,
        cpc: this.currencyPipe.transform(statOne.cpc ? statOne.cpc : 0),
        ctr: this.percentPipe.transform(statOne.ctr, "1.2-2"),
        revenue: statOne.revenue,
      };
      exportCsv.push(stat);
    }
    this.exportDataService.exportToCsv(exportCsv, this.tagRows.nickName ? this.tagRows.nickName : this.tagRows.name, ['date', 'subid', 'country', 'clicks', 'searches', 'cpc', 'ctr', 'revenue']);
  }

  public async updateReportingFiltering(range: { startDate: string; endDate: string; }) {
    this.range = range;
    this.loadingIndicator = true;
    this.statData = [];
    if (this.paramApiKey && this.paramTagId) {
      this.statData = await this.getPublisherAPIData(this.selectedCompany, this.paramApiKey, this.paramTagId, this.range.startDate, this.range.endDate);
      this.chartData = await this.getChartMetrics(this.selectedCompany, this.paramApiKey, this.paramTagId, this.range.startDate, this.range.endDate);
      this.loadingIndicator = false;
      this.cdr.markForCheck();
    }
  }

  //Gets the Selected Company from Local Storage
  getSelectedCompanyFromLocalStorage() {
    return this.userService.getSelectedCompanyFromLocalStorage();
  }

  async getPublisherAPIData(company: any, apiKey: string, tagId: string, startDate: string, endDate: string) {
    try {
      const response = await this.publisherApiService.getPublisherAPIStats(company, apiKey, tagId, startDate, endDate).toPromise();
      return response.slice().sort((a, b) => b.date - a.date);
    } catch (error) {
      return error;
    }
  }

  toggleExpandRow(row) {
    // console.log('Toggled Expand Row!', row);
    this.table.rowDetail.toggleExpandRow(row);
  }

  onDetailToggle(event) {
    // console.log('Detail Toggled', event);
  }

}
