import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { PublisherApiComponent } from './publisher-api.component';
import { TagApiComponent } from './tag-api/tag-api.component';

const routes: Routes = [
  {
    path: '',
    component: PublisherApiComponent,
    children: [
      {
        path: ':fullname/:apiKey/:tagId',
        component: TagApiComponent,
        pathMatch: 'full',
      },

      { path: '', redirectTo: '', pathMatch: 'full' },
      { path: '**', redirectTo: '', pathMatch: 'full' },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PublisherApiRoutingModule { }
