import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { environment } from '../../../../environments/environment';
import { Observable } from 'rxjs';

const API_PUBLISHER_URL = `${environment.apiUrl}/publisher-api`;

@Injectable({
  providedIn: 'root'
})
export class PublisherAPIService {

  constructor(private http: HttpClient) { }

  getPublisherAPIStats(company, apiKey, tagId, startDate, endDate): Observable<any> {
    return this.http.get<any>(API_PUBLISHER_URL + '/', {
      params: { company: company, apiKey: apiKey, tag: tagId, startDate: startDate, endDate: endDate },
    });
  }

  getSummaryMetrics(company, startDate, endDate) {
    return this.http.get<any>(API_PUBLISHER_URL + '/summary_metrics', {
      params: { company: company, startDate: startDate, endDate: endDate },
    });
  }
}
